<template>
<div>
  <!-- 포스트페이지 -->
  <div v-if="탭===0">
 <Post :인스타="인스타"/>
  </div>



 <!-- 필터선택페이지 -->
 <div v-if="탭===1">
  <div class="upload-image" :class="필터명" :style="{backgroundImage : `url(${주소})`}"></div>
  <div class="filters">
  <FilterBox :주소="주소" v-for="필터 in filter" :key="필터" :필터="필터">
  </FilterBox>
  </div>
 </div>



<!-- 글작성페이지 -->
<div v-if="탭===2">
  <div :class="필터명" class="upload-image" :style="{backgroundImage : `url(${주소})`}"></div>
  <div class="write">
    <textarea class="write-box" @change="$emit('write',$event.target.value)">write!</textarea>
  </div>
</div>
</div>

<div v-if="탭===3">
<MyPage/>
</div>


</template>

<style>
 
</style>

<script>
import Post from './Post.vue';
import FilterBox from './FilterBox.vue';
import MyPage from './MyPage.vue';
export default {
   name : 'Container',
  components: {
    Post,
    FilterBox,
    MyPage,
  },
  props : {
    인스타 : Array,
    탭 : Number,
    주소 : String,
  },
  data(){
    return{
      filter : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
"inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
"reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
      필터명 : '',

    }
  },
  mounted(){
    this.emitter.on('필터명',(a)=>{
      this.필터명 = a;
    })
  }
}
</script>


<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
