<template>
<div style="padding : 10px">
  <h4>팔로워</h4>
  <input placeholder="🔍" @input="search($event.target.value)"/>
  <div class="post-header" v-for="(a,i) in follower" :key="i">
    <div class="profile" :style="{background : `url(${follower[i].image})`}"></div>
    <span class="profile-name">{{follower[i].name}}</span>
  </div>
</div>
</template>

<script>
import { ref} from '@vue/reactivity';
import axios from 'axios';
import { onMounted } from '@vue/runtime-core';
export default {
  name : 'MyPage',
  setup(){
    let follower = ref([]);
    let followerOrigin = ref([]);

    onMounted(()=>{
      axios.get('/follower.json').then((res)=>{
      follower.value = res.data;
      followerOrigin.value = [...res.data];
    });
    });

    function search(검색어){
      let newFollower = followerOrigin.value.filter(a=>{
        return a.name.indexOf(검색어) != -1
      });
      
      follower.value = [...newFollower];
    }

    return {followerOrigin,follower, search};
  }
}
</script>

