import axios from 'axios';
import {createStore} from 'vuex';

const store = createStore({
  state(){
    return{
      likes : [30,40,50],
      좋아요눌렀니 : false,
      more : {},
    }
  },
  mutations : {
    좋아요(state,i){
      if(state.좋아요눌렀니===false){
        state.likes[i]++;
        state.좋아요눌렀니=true;
      }else{
        state.likes[i]--;
        state.좋아요눌렀니=false;
      }
    },
    setMore(state, data){
      state.more = data;      
    },
  },
  actions : {
    getData(context){
      axios.get(`https://codingapple1.github.io/vue/more1.json`)
      .then((res)=>{
       context.commit('setMore',res.data);
      });
    }
  }
})


export default store;