<template>
<div class="header">
    <ul class="header-button-left" v-if="tab===1">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="tab===1" @click="tab++">Next</li>
      <li v-if="tab === 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

<Container :인스타="insta" :탭="tab" :주소="url" @write="content = $event"/>
<button @click="more" v-if="tab===0">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" multiple type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus" v-if="tab===0">+</label>
    </ul>
 </div>




</template>

<script>
import Container from './components/Container.vue';
import Data from './assets/data';
import axios from 'axios';
import {mapState} from 'vuex';

export default {
  name: 'App',
  components: {
    Container

  },
  data(){
    return{
      insta : Data,
      tab : 3,
      더보기 : 0,
      url : '',
      content : '',
      필터명 : '',
    }
  },
  computed : {
    ...mapState(['name', 'age', 'likes']),
  },
  methods : {
    more(){
      axios.get(`https://codingapple1.github.io/vue/more${this.더보기}.json`)
      .then((res)=>{
        this.insta.push(res.data);
        this.더보기++;
      });
    },
    upload(e){
      let 파일 = e.target.files
      console.log(파일[0]);
      const url = URL.createObjectURL(파일[0]);
      this.url = url;
      this.tab++;
    },
    publish(){
      let 내게시물 =   {
    name: "Kim Hyun",
    userImage: "https://placeimg.com/100/100/arch",
    postImage: this.url,
    likes: 36,
    date: "May 15",
    liked: false,
    content: this.content,
    filter: this.필터명,
  };
      this.insta.unshift(내게시물);
      this.tab = 0;
    },
  },
  mounted(){
    this.emitter.on('필터명',(a)=>{
      this.필터명 = a;
    })
  },
}
</script>

