<template>
<div :class="필터" class="filter-item" :style="{backgroundImage : `url(${주소})`}"
@click="fire">
<p class="filter-name">{{필터}}</p>
</div> 
</template>

<script>
export default {
  name : 'FilterBox',
  props : {
    주소 : String,
    필터 : String,
  },
  methods : {
    fire(){
      this.emitter.emit('필터명',this.필터);
    }
  },

}
</script>

<style>
  .filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
  position: relative;
}
.filter-name{
  color: black;
  position: absolute;
  top: -15px;
  left: 0px;
  background: wheat;
  font-size: 14px;
}
</style>